<template>
  <div id="loading-container" class="vs-con-loading__container">
    <div class="alert-container" v-if="alertActive">
      <vs-alert :active.sync="alertActive" :color="alertVariant" closable icon-pack="feather" close-icon="icon-x" class="mb-5">
        <p>{{ alertMessage }}</p>
      </vs-alert>
    </div>

    <nv-card>
      <form-wizard ref="wizard"
                   color="rgba(var(--vs-primary), 1)"
                   :title="null" :subtitle="null"
                   :nextButtonText="$t('NEXT')"
                   :backButtonText="$t('PREVIOUS')">
        <template slot="custom-buttons-left" slot-scope="props">
          <vs-button class="mr-2" color="primary" type="border" @click="cancel" v-if="props.activeTabIndex === 0">{{ $t('CANCEL') }}</vs-button>
        </template>
        <template slot="finish">
          <div class="dropdown-button-container">
            <vs-button class="btnx" type="filled" @click="createBranch('browse')">{{ $t('SUBMIT') }}</vs-button>
            <vs-dropdown vs-trigger-click>
              <vs-button class="btn-drop" type="filled" icon="fa-angle-down" icon-pack="far"></vs-button>
              <vs-dropdown-menu>
                <vs-dropdown-item @click="createBranch('stay')">
                  <span>{{ $t('SUBMIT_STAY') }}</span>
                </vs-dropdown-item>
                <vs-dropdown-item @click="createBranch('view')">
                  <span>{{ $t('SUBMIT_VIEW') }}</span>
                </vs-dropdown-item>
              </vs-dropdown-menu>
            </vs-dropdown>
          </div>
        </template>

        <tab-content :title="$t('GENERAL')" icon="far fa-file-alt">
          <div class="nv-row mb-4">
            <div class="nv-col sm:w-1/2 w-full required">
              <label class="vs-input--label">{{ $t('BRANCH_NAME') }}</label>
              <vs-input class="w-full" v-model="branchName"/>
            </div>
            <div class="nv-col sm:w-1/2 w-full required">
              <label class="vs-input--label">{{ $t('BANK') }}</label>
              <bank-lookup :title="$t('BANK')" valueField="bankId" displayField="bankName" @select-record="selectBankRecord"/>
            </div>
          </div>
          <div class="nv-row mb-4">
            <div class="nv-col sm:w-1/2 w-full">
              <label class="vs-input--label">{{ $t('BRANCH_CODE') }}</label>
              <vs-input class="w-full" v-model="branchCode"/>
            </div>
            <div class="nv-col sm:w-1/2 w-full">
              <label class="vs-input--label">{{ $t('RECORD_KEY') }}</label>
              <vs-input class="w-full" v-model="recordKey"/>
            </div>
          </div>
          <div class="nv-row mb-4">
            <div class="nv-col sm:w-1/2 w-full">
              <label class="vs-input--label">{{ $t('SWIFT_CODE') }}</label>
              <vs-input class="w-full" v-model="swiftCode"/>
            </div>
            <div class="nv-col sm:w-1/2 w-full">
              <label class="vs-input--label">{{ $t('LOCAL_CODE') }}</label>
              <vs-input class="w-full" v-model="localCode"/>
            </div>
          </div>
          <div class="nv-row mb-4">
            <div class="nv-col w-full">
              <label class="vs-input--label">{{ $t('EXTERNAL_ID') }}</label>
              <vs-input class="w-full" v-model="externalBranchId"/>
            </div>
          </div>
          <div class="nv-row mb-4">
            <div class="nv-col w-full">
              <vs-checkbox v-model="acceptsPayments">{{ $t('ACCEPTS_PAYMENTS') }}</vs-checkbox>
            </div>
          </div>
          <div class="nv-row mb-4">
            <div class="nv-col w-full">
              <vs-checkbox v-model="disabled">{{ $t('DISABLED') }}</vs-checkbox>
            </div>
          </div>
        </tab-content>

        <tab-content :title="$t('ADDRESS')" icon="far fa-map">
          <div class="nv-row mb-4">
            <div class="nv-col sm:w-1/2 w-full">
              <label class="vs-input--label">{{ $t('ADDRESS_1') }}</label>
              <vs-input class="w-full" v-model="address1"/>
            </div>
            <div class="nv-col sm:w-1/2 w-full">
              <label class="vs-input--label">{{ $t('ADDRESS_2') }}</label>
              <vs-input class="w-full" v-model="address2"/>
            </div>
          </div>
          <div class="nv-row mb-4">
            <div class="nv-col sm:w-1/2 w-full">
              <label class="vs-input--label">{{ $t('DISTRICT') }}</label>
              <vs-input class="w-full" v-model="district"/>
            </div>
            <div class="nv-col sm:w-1/2 w-full">
              <label class="vs-input--label">{{ $t('CITY') }}</label>
              <vs-input class="w-full" v-model="city"/>
            </div>
          </div>
          <div class="nv-row mb-4">
            <div class="nv-col sm:w-1/2 w-full">
              <label class="vs-input--label">{{ $t('PROVINCE') }}</label>
              <vs-input class="w-full" v-model="province"/>
            </div>
            <div class="nv-col sm:w-1/2 w-full required">
              <label class="vs-input--label">{{ $t('COUNTRY') }}</label>
              <country-lookup :title="$t('COUNTRY')" valueField="geoId" displayField="geoName" @select-record="selectCountryRecord"/>
            </div>
          </div>
          <div class="nv-row mb-4">
            <div class="nv-col sm:w-1/2 w-full">
              <label class="vs-input--label">{{ $t('POSTAL_CODE') }}</label>
              <vs-input class="w-full" v-model="postalCode"/>
            </div>
            <div class="nv-col sm:w-1/2 w-full">
              <label class="vs-input--label">{{ $t('POSTAL_CODE_EXT') }}</label>
              <vs-input class="w-full" v-model="postalCodeExt"/>
            </div>
          </div>
          <div class="nv-row mb-4">
            <div class="nv-col sm:w w-full">
              <label class="vs-input--label">{{ $t('PO_BOX') }}</label>
              <vs-input class="w-full" v-model="poBox"/>
            </div>
          </div>
        </tab-content>

        <tab-content :title="$t('FUNCTIONS')" icon="far fa-check">
          <ul class="flex justify-start">
            <li class="mr-5" v-for="(bankFunction, index) in bankFunctionList" :key="index">
              <vs-checkbox v-model="functions" :vs-value="bankFunction.functionId" >
                <i class="text-primary mr-1" :class="bankFunction.iconClass"></i> <span :class="disabledClass(bankFunction)">{{ bankFunction.functionName }}</span>
              </vs-checkbox>
            </li>
          </ul>
        </tab-content>
      </form-wizard>
    </nv-card>
  </div>
</template>

<script>
import AlertMixin from '@/mixins/commons/AlertMixin'
import DisabledClassMixin from '@/mixins/commons/DisabledClassMixin'
import LoadingMixin from '@/mixins/commons/LoadingMixin'
import BranchService from '@/services/bank/BranchService'
import BankLookup from '@/views/bank/bank/BankLookup'
import CountryLookup from '@/views/nucleus/country/CountryLookup'
import BankFunctionService from '@/services/bank/BankFunctionService'
import { FormWizard, TabContent } from 'vue-form-wizard'

export default {
  name: 'branch-create',
  mixins: [AlertMixin, DisabledClassMixin, LoadingMixin],
  components: {
    FormWizard,
    TabContent,
    BankLookup,
    CountryLookup
  },
  data: function () {
    return {
      branchName: '',
      bankId: '',
      branchCode: '',
      recordKey: '',
      swiftCode: '',
      externalBranchId: '',
      localCode: '',
      address1: '',
      address2: '',
      district: '',
      city: '',
      province: '',
      countryGeoId: '',
      postalCode: '',
      postalCodeExt: '',
      poBox: '',
      acceptsPayments: false,
      disabled: false,
      bankFunctionList: [],
      functions: []
    }
  },
  methods: {
    async load () {
      let self = this
      BankFunctionService.bankFunctions({
      }).then(function (response) {
        self.bankFunctionList = response.data.functionList || []
      })
    },
    selectBankRecord (record) {
      this.bankId = record.bankId || ''
    },
    selectCountryRecord (record) {
      this.countryGeoId = record.geoId || ''
    },
    createBranch (nextAction) {
      let self = this
      this.startLoading('loading-container')
      BranchService.createBranch({
        branchName: this.branchName,
        countryGeoId: this.countryGeoId,
        bankId: this.bankId,
        branchCode: this.branchCode,
        swiftCode: this.swiftCode,
        externalBranchId: this.externalBranchId,
        localCode: this.localCode,
        recordKey: this.recordKey,
        address1: this.address1,
        address2: this.address2,
        district: this.district,
        city: this.city,
        province: this.province,
        postalCode: this.postalCode,
        postalCodeExt: this.postalCodeExt,
        poBox: this.poBox,
        functions: this.functions.join(','),
        acceptsPayments: this.acceptsPayments ? 'Y' : 'N',
        disabled: this.disabled ? 'Y' : 'N'
      }).then(function (response) {
        self.stopLoading('loading-container')
        if (response.status === 200) {
          if (nextAction === 'stay') {
            self.showSuccessAlert(response.data.messages)
            self.$refs.wizard.reset()
          } else if (nextAction === 'view') {
            self.$router.push({ name: 'ViewBranch', params: { id: response.data.branchId } })
          } else {
            self.$router.push({ name: 'BrowseBranches' })
          }
        }
      }).catch(function (error) {
        self.stopLoading('loading-container')
        self.showDangerAlert(error.response.data.errors)
      })
    },
    cancel () {
      this.$router.back()
    }
  },
  mounted () {
    this.load()
  }
}
</script>
